@import "../../shared/CSS/blueTheme.css";

* {
    margin: 0;
    padding: 0;
}

ul {
    padding: 0 !important;
    margin: 0 !important;
}

li {
    list-style: none !important;
}

body {
    background-color: var(--asy-main-background-color);
}


/* width */
::-webkit-scrollbar {
    width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
    display: none;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #333;
    border-radius: 10px;
    display: none;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #333;
    display: block;
}

.side-menu {
    position: fixed;
    background: #06163a;
    width: 300px;
    height: 100%;
    margin-top: 80px;
    box-sizing: border-box;
    padding: 30px 20px;
    transition: width 0.2s ease-in;
    padding-bottom: 135px;
    z-index: 1;
}

.side-menu.inactive {
    width: 80px;
}

.side-menu .top-section {
    position: relative;
}

.side-menu .top-section .logo {
    width: 40px;
    height: 40px;
    overflow: hidden;
    display: inline-block;
}

.side-menu .top-section .logo img {
    max-width: 100%;
    max-height: 100%;
}

.side-menu .top-section .toggle-menu-btn {
    color: #1bdcff;
    font-size: 20px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    cursor: pointer;
}

.side-menu.inactive .top-section .toggle-menu-btn {
    right: 10px;
    color: #1bdcff;
}

.side-menu .search-controller {
    color: #fff;
    position: relative;
}

.side-menu .search-controller .search-btn {
    width: 40px;
    height: 35px;
    position: absolute;
    background: transparent;
    border: 0;
    font-size: 24px;
    color: #666;
}

.side-menu .search-controller input[type="text"] {
    border: 0;
    outline: none;
    height: 35px;
    background: #333;
    border-radius: 5px;
    display: block;
    margin: 20px 0;
    width: 100%;
    box-sizing: border-box;
    padding-left: 40px;
    color: #666;
}

.side-menu .main-menu .menu-item {
    color: #c5c5c5;
    background-color: #000922;
    text-decoration: none;
    font-size: 15px;
    display: block;
    font-weight: 600;
    cursor: pointer;
    margin-top: 15px !important;
    overflow: auto !important;
}

.side-menu .main-menu .menu-item.active {
    background: #1bdcff;
    border-radius: 5px;
    color: #fff;
}

.side-menu .main-menu .menu-item:hover {
    background: #252831;
    color: #1bdcff;
    border-left: 4px solid #1bdcff;
    border-radius: 8px;
    cursor: pointer;
    text-decoration: none;
}

.side-menu .main-menu {
    margin: 5px 0;
    position: relative;
    max-height: 100%;
    overflow-y: auto;
    overflow-x: auto;
    margin-top: 20px;
}

/* not required any more */
.side-menu.inactive .main-menu {}

.side-menu .main-menu .menu-item .menu-icon {
    display: inline-block;
    width: 40px;
    height: 40px;
    font-size: 20px;
    line-height: 40px;
    text-align: center;
}

.side-menu .main-menu .menu-item span {
    position: absolute;
    display: inline-block;
    line-height: 40px;
    font-size: 1rem;
    opacity: 1;
    transition: opacity 0.2s ease-in;
}

.side-menu.inactive .main-menu .menu-item span {
    opacity: 0;
    width: 0;
    height: 0;
    overflow: hidden;
}

.side-menu .main-menu .sub-menu {
    color: #c5c5c5;
    margin-left: 20px;
    border-left: 1px solid #666;
    box-sizing: border-box;
    padding-left: 30px;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease-in;
}

.side-menu .main-menu .sub-menu a.active:hover {
    background: #252831;
    color: #1bdcff;
    border-left: 4px solid #1bdcff;
    border-radius: 8px;
    cursor: pointer;
    text-decoration: none;
}

.side-menu .main-menu .sub-menu a:hover {
    background: #252831;
    color: #1bdcff;
    border-left: 4px solid #1bdcff;
    border-radius: 8px;
    cursor: pointer;
    text-decoration: none;
}

.side-menu .main-menu .sub-menu.active {
    max-height: 200px;
}

.side-menu .main-menu .sub-menu a {
    display: block;
    margin: 5px 0;
    font-size: 15px;
    font-weight: 600;
    text-decoration: none;
    color: #c5c5c5;
    box-sizing: border-box;
    padding: 5px;
}

.side-menu .main-menu .sub-menu a.active {
    /* change in background, not recorded in video */
    background: #1bdcff;
    display: block;

    border-radius: 5px;
    color: #fff;
}

.side-menu-footer {
    width: 100%;
    background: #333;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 20px;
    box-sizing: border-box;
}

.side-menu-footer .user-info {
    display: inline-block;
    margin: 0 10px;
    color: #fff;
    position: absolute;
    opacity: 1;
    transition: opacity 0.2s ease-in;
}

.side-menu.inactive .side-menu-footer .user-info {
    opacity: 1;
    width: 0;
    height: 0;
    overflow: hidden;
}

.side-menu-footer .user-info h5 {
    font-size: 15px;
}

.side-menu-footer .user-info p {
    font-size: 14px;
}

.divider {
    width: 100% !important;
    height: 1px !important;
    border-radius: 1px !important;
    background: #333;
}

.container {
    margin-left: 300px !important;
    transition: margin-left 0.2s ease-in !important;
    padding: 50px !important;
    box-sizing: border-box !important;
}

.container.inactive {
    margin-left: 80px !important;
}
