 } */

 .radio_btns {
  display: block;
  position: relative;
  padding-left: 20px;
  margin-left: 2rem !important;
  margin-top: 1rem;
  cursor: pointer;
}

.radio_btns input {
  /* position: absolute; */
  opacity: 0;
  cursor: pointer;
}

.check {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  margin-top:7px;
  padding: 4px;
  border: solid 1px #707070;
  background-color: #fff;
  cursor:pointer;
}

.radio_btns:hover input ~ .check {
   background-color: white;
}

.radio_btns input:checked ~ .check {
  background-color: white;
}

.check:after {
  content: "";
  /* position: absolute; */
  display: none;
}

.radio_btns input:checked ~ .check:after {
  display: block;
}

.radio_btns .check:after {
 	top: 4px;
	left: 4px;
	width: 6px;
	height: 6px;
	border-radius: 50%;
	background: #1bdcff;  
}  



