.CardArea{
   align-content: center;
   justify-content: center;
   border-radius: .3rem;
   display: flex;
   flex: auto;
   flex-wrap: wrap;
 }
    
 .leavesdashboard{
   border: .5px solid #ccc;
   background-color: whitesmoke;
 }
 
 .card.polls{
   justify-content: space-between;
   width: 30%;
   border: 1px solid #ccc;
   border-radius: 2px;
   display: flex;
   flex: auto;
   margin: 10px;
   margin-bottom: 0px !important;
   margin-bottom: -5px !important;
   border-radius: 8px;
   border: solid 1px rgba(208, 208, 208, 0.42);
 }
 
 .card-body.polls{
   display: inline-block;
   border: none;
   outline: none;
 }

















