.profileMainCardBorder {
	flex-shrink: 0;
	width: 80%;
	min-width: 326px;
	/* margin-left: 20px; */
	/* max-width: fit-content; */
	padding: 20px;
	border: none;
	box-shadow: rgb(154 161 171 / 15%) 0px 0px 35px 0px;
	-webkit-box-flex: 1;
	flex: 1 1 auto;
	background-color: white;
}
.profilePendingApproval {
	box-shadow: rgb(154 161 171 / 65%) 0px 0px 35px 0px;
	-webkit-box-flex: 1;
	width: 70%;
}
.profileImage {
	max-width: 6rem;
	max-height: 6rem;
	width: auto;
	height: auto;
	border-radius: 50%;
}
.profileEditButton {
	padding: 0.28rem 0.8rem;
	font-size: 0.875rem;
	border-radius: 0.15rem;
	color: rgb(255, 255, 255);
	background-color: #f01111;
	/*background-color: #1bdcff;*/
	border-color: rgb(252, 255, 254);
	font-weight: 400;
	line-height: 1.5;
	text-align: center;
	vertical-align: middle;
	cursor: pointer;
	border: 1px solid transparent;
	padding: 6px 23px;
	font-size: 0.9rem;
	border-radius: 0.15rem;
	transition: color 0.15s ease-in-out 0s, background-color 0.15s ease-in-out 0s, border-color 0.15s ease-in-out 0s,
		box-shadow 0.15s ease-in-out 0s, -webkit-box-shadow 0.15s ease-in-out 0s;
}
.profileLabel {
	font-size: 14px;
	color: #6c757d;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans',
		'Helvetica Neue', sans-serif;
}
.profileLabel::first-letter {
	text-transform: capitalize;
}
.profileLabelValues {
	display: inline;
	margin-left: 0.75rem;
	color: #98a6ad;
	font-size: 14px;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans',
		'Helvetica Neue', sans-serif;
}
.headLine {
	background-color: rgb(241, 109, 109);
	color: white;
}
.alertImg {
	width: 20px;
	height: 25px;
	margin-left: 10px;
}
nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
	color: #fff !important;
	background-color: #1bdcff !important;
}

.scrollPanel {
	height: 300px;
	width: 100%;
	overflow-x: hidden;
	overflow-y: scroll;
}
::-webkit-scrollbar {
	display: none;
}

.link {
	/*	text-decoration: none;*/
	color: black;
}
.pendingApprovals {
	position: relative;
	display: block;
	background-color: #fff;
	color: aqua;
	padding: 0;
	margin:0;
}


.profileContent{
	padding:10px;
}