
.TaskPageContent{
	padding:10px;
}
.SubBox{
  margin-bottom: 7px; 
  background-color: rgb(255, 255, 255);
  padding: 5px; 
  border-radius: 3px;
  height: auto; 
  }
.card{
  margin: 5px;
  border: 1px solid rgb(155, 152, 152);
  background-color: #72e9ff;
  box-shadow: 4px 6px 7px 0px rgba(110, 132, 156, 0.58);  
  border-radius: 5px;
  }
 .card-body{
   padding: 2px;
   height: 100%;
  }
.col{
  padding-right: 0px;
  padding-left: 0px;
  }
#row{
   margin-left: 0px;
   margin-right: 0px;
  }
  .GripsLines{
    width: 20px;
  }

.taskLabel {
	font-size: 14px;
	color: #6c757d;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans',
		'Helvetica Neue', sans-serif;
}
.taskLabel::first-letter {
	text-transform: capitalize;
}
.taskLabelValues {
	display: inline;
	color: #98a6ad;
	font-size: 14px;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans',
		'Helvetica Neue', sans-serif;
}

@media screen and (max-width: 500px) {
  .col{
  margin-top:3px;
  }
}